import $ from '../../util/util';
import cron from './cron';
import './scroll';
import * as util from './util';
// import pickerTpl from './picker.html';
// import groupTpl from './group.html';

const pickerTpl = '<div class="<%= className %>">\
<div class="mtui-mask"></div>\
<div class="mtui-js-picker">\
    <div class="mtui-js-picker__hd">\
        <a href="javascript:;" data-action="cancel" class="mtui-js-picker__action"></a>\
        <a href="javascript:;" data-action="select" class="mtui-js-picker__action" id="mtui-js-picker-confirm"></a>\
    </div>\
    <% if(!!search){ %>\
    <div class="mtui-js-picker__searchBox">\
        <input class="mtui-js-picker__search" type="text" placeholder="<%= searchPlaceholder %>" />\
    </div>\
    <% } %>\
    <div class="mtui-js-picker__bd"></div>\
</div>\
</div>';
const groupTpl = '<div class="mtui-js-picker__group">\
<div class="mtui-js-picker__mask"></div>\
<div class="mtui-js-picker__indicator"></div>\
<div class="mtui-js-picker__content"></div>\
</div>';

function Result(item) {
    if(typeof item != 'object'){
        item = {
            label: item,
            value: item
        };
    }
    $.extend(this, item);
}
Result.prototype.toString = function () {
    return this.value;
};
Result.prototype.valueOf = function () {
    return this.value;
};

let _sington;
let temp = {}; // temp 

/**
 * picker 
 * @param {array} items pickerpickerpickerexample
 * @param {Object} options 
 * @param {number=} [options.depth] picker(picker) 1-3items
 * @param {string=} [options.id=default] pickeriddefaultValueid
 * @param {string=} [options.className] 
 * @param {string=} [options.container] 
 * @param {array=} [options.defaultValue] value
 * @param {function=} [options.onChange] picker
 * @param {function=} [options.onConfirm] ""(Array)picker
 * @param {function=} [options.onClose] picker
 *
 * @example
 * // picker
 * mtui.picker([
 * {
 *     label: '',
 *     value: 0,
 *     disabled: true // 
 * },
 * {
 *     label: '',
 *     value: 1
 * },
 * {
 *     label: '',
 *     value: 3
 * },
 * {
 *     label: '',
 *     value: 4,
 * }
 * ], {
 *    className: 'custom-classname',
 *    container: 'body',
 *    defaultValue: [3],
 *    onChange: function (result) {
 *        console.log(result)
 *    },
 *    onConfirm: function (result) {
 *        console.log(result)
 *    },
 *    id: 'singleLinePicker'
 * });
 *
 * @example
 * // picker
 * mtui.picker([
 *     {
 *         label: '1',
 *         value: '1'
 *     }, {
 *         label: '2',
 *         value: '2'
 *     }, {
 *         label: '3',
 *         value: '3'
 *     }
 * ], [
 *     {
 *         label: 'A',
 *         value: 'A'
 *     }, {
 *         label: 'B',
 *         value: 'B'
 *     }, {
 *         label: 'C',
 *         value: 'C'
 *     }
 * ], {
 *     defaultValue: ['3', 'A'],
 *     onChange: function (result) {
 *         console.log(result);
 *     },
 *     onConfirm: function (result) {
 *         console.log(result);
 *     },
 *     id: 'multiPickerBtn'
 * });
 *
 * @example
 * // picker
 * mtui.picker([
 * {
 *     label: '',
 *     value: 0,
 *     children: [
 *         {
 *             label: '',
 *             value: 1
 *         },
 *         {
 *             label: '',
 *             value: 2
 *         }
 *     ]
 * },
 * {
 *     label: '',
 *     value: 1,
 *     children: [
 *         {
 *             label: '',
 *             value: 1,
 *             disabled: true // 
 *         },
 *         {
 *             label: '',
 *             value: 2
 *         },
 *         {
 *             label: '',
 *             value: 3
 *         }
 *     ]
 * },
 * {
 *     label: '',
 *     value: 3,
 *     children: [
 *         {
 *             label: '',
 *             value: 1
 *         },
 *         {
 *             label: '',
 *             value: 2
 *         }
 *     ]
 * }
 * ], {
 *    className: 'custom-classname',
 *    container: 'body',
 *    defaultValue: [1, 3],
 *    onChange: function (result) {
 *        console.log(result)
 *    },
 *    onConfirm: function (result) {
 *        console.log(result)
 *    },
 *    id: 'doubleLinePicker'
 * });
 */
function picker() {
    if (_sington) return _sington;

    // 
    const options = arguments[arguments.length - 1];
    const defaults = $.extend({
        id: 'default',
        className: '',
        container: 'body',
        search: false,
        searchPlaceholder: '',
        onChange: $.noop,
        onConfirm: $.noop,
        onClose: $.noop
    }, options);

    // 
    let items;
    let isMulti = false; // 
    if (arguments.length > 2) {
        let i = 0;
        items = [];
        while (i < arguments.length - 1) {
            items.push(arguments[i++]);
        }
        isMulti = true;
    } else {
        items = arguments[0];
    }

    if(Object.prototype.toString.call(items[0])  === '[object Array]'){
        defaults.search = false
    }

    // 
    temp[defaults.id] = temp[defaults.id] || [];
    const result = [];
    const lineTemp = temp[defaults.id];
    const $picker = $($.render(pickerTpl, defaults));
    let depth = options.depth || (isMulti ? items.length : util.depthOf(items[0])), groups = '';

    // 
    function show(){
        $(defaults.container).append($picker);

        // . fix IOS10
        $.getStyle($picker[0], 'transform');

        $picker.find('.mtui-mask').addClass('mtui-animate-fade-in');
        $picker.find('.mtui-js-picker').addClass('mtui-animate-slide-up');
    }
    function _hide(callback){
        _hide = $.noop; // 

        $picker.find('.mtui-mask').addClass('mtui-animate-fade-out');
        $picker.find('.mtui-js-picker')
            .addClass('mtui-animate-slide-down')
            .on('animationend webkitAnimationEnd', function () {
                $picker.remove();
                _sington = false;
                defaults.onClose();
                callback && callback();
            });
    }
    function hide(callback){ _hide(callback); }

    // 
    function scroll(items, level) {
        if (lineTemp[level] === undefined && defaults.defaultValue && defaults.defaultValue[level] !== undefined) {
            // defaultValue
            const defaultVal = defaults.defaultValue[level];
            let index = 0, len = items.length;

            if(typeof items[index] == 'object'){
                for (; index < len; ++index) {
                    if (defaultVal == items[index].value) break;
                }
            }else{
                for (; index < len; ++index) {
                    if (defaultVal == items[index]) break;
                }
            }
            if (index < len) {
                lineTemp[level] = index;
            } else {
                console.warn('Picker has not match defaultValue: ' + defaultVal);
            }
        }
        $picker.find('.mtui-js-picker__group').eq(level).scroll({
            items: items,
            temp: lineTemp[level],
            onChange: function (item, index) {
                //result
                if (item) {
                    result[level] = new Result(item);
                } else {
                    result[level] = null;
                }
                lineTemp[level] = index;

                if (isMulti) {
                    if(result.length == depth){
                        defaults.onChange(result);
                    }
                } else {
                    /**
                     * @
                     * 1. 0
                     * 2. 
                     *
                     * @
                     * 1. callonChange
                     * 2. onChangecall
                     */
                    if (item.children && item.children.length > 0) {
                        $picker.find('.mtui-js-picker__group').eq(level + 1).show();
                        !isMulti && scroll(item.children, level + 1); // children
                    } else {
                        //test
                        const $items = $picker.find('.mtui-js-picker__group');
                        $items.forEach((ele, index) => {
                            if (index > level) {
                                $(ele).hide();
                            }
                        });

                        result.splice(level + 1);

                        defaults.onChange(result);
                    }
                }
            },
            onConfirm: defaults.onConfirm
        });
    }


    let _depth = depth;
    while (_depth--) {
        groups += groupTpl;
    }

    $picker.find('.mtui-js-picker__bd').html(groups);
    show();

    if (isMulti) {
        items.forEach((item, index) => {
            scroll(item, index);
        });
    } else {
        scroll(items, 0);
    }

    $picker
        .on('input',".mtui-js-picker__search",function(ev){
            let that = this,
                itemss = [];
            items.map((item) => {
                let value = typeof item == 'object' ? item.label : item;
                if(value.indexOf(that.value) > -1){
                    itemss.push(item);
                }
            });
            if(this.value != ''){
                if(itemss.length > 0){
                    scroll(itemss,0)
                }else{
                    scroll([{label:''}],0)
                }
            }else{
                scroll(items,0)
            }
        })
        .on('click', '.mtui-mask', function () { hide(); })
        .on('click', '.mtui-js-picker__action', function () { hide(); })
        .on('click', '#mtui-js-picker-confirm', function () {
            defaults.onConfirm(result);
        });

    _sington = $picker[0];
    _sington.hide = hide;
    return _sington;
}

/**
 * datePicker picker
 * @param options 
 * @param {string=} [options.id=datePicker] picker
 * @param {number=|string|Date} [options.start=2000]  `Number`  `String`  'YYYY-MM-DD' `Date`  Date
 * @param {number=|string|Date} [options.end=2030] 
 * @param {string=} [options.cron=* * *] cron  dayOfMonth[1-31]month[1-12]  dayOfWeek[0-6]-
 * @param {string=} [options.className] 
 * @param {array=} [options.defaultValue] value,  [1991, 6, 9]
 * @param {function=} [options.onChange] picker
 * @param {function=} [options.onConfirm] ""(Array)picker
 *
 *@example
 * // 1
 * mtui.datePicker({
 *     start: 1990,
 *     end: 2000,
 *     defaultValue: [1991, 6, 9],
 *     onChange: function(result){
 *         console.log(result);
 *     },
 *     onConfirm: function(result){
 *         console.log(result);
 *     },
 *     id: 'datePicker'
 * });
 *
 * // 2
 * mtui.datePicker({
 *      start: new Date(), // 
 *      end: 2030,
 *      defaultValue: [2020, 6, 9],
 *      onChange: function(result){
 *          console.log(result);
 *      },
 *      onConfirm: function(result){
 *          console.log(result);
 *      },
 *      id: 'datePicker'
 *  });
 *
 *  // 3
 * mtui.datePicker({
 *      start: new Date(), // 
 *      end: 2030,
 *      cron: '* * 0,6',  // 
 *      onChange: function(result){
 *          console.log(result);
 *      },
 *      onConfirm: function(result){
 *          console.log(result);
 *      },
 *      id: 'datePicker'
 *  });
 *
 *  // 4
 * mtui.datePicker({
 *      start: new Date(), // 
 *      end: 2030,
 *      cron: '1-10 * *',  // 1-10
 *      onChange: function(result){
 *          console.log(result);
 *      },
 *      onConfirm: function(result){
 *          console.log(result);
 *      },
 *      id: 'datePicker'
 *  });
 */
function datePicker(options) {
    const defaults = $.extend({
        id: 'datePicker',
        onChange: $.noop,
        onConfirm: $.noop,
        start: 2000,
        end: 2030,
        cron: '* * *'
    }, options);

    //  startend  Number 
    if (typeof defaults.start === 'number') {
        defaults.start = new Date(`${defaults.start}/01/01`);
    }
    else if (typeof defaults.start === 'string') {
        defaults.start = new Date(defaults.start.replace(/-/g, '/'));
    }
    if (typeof defaults.end === 'number') {
        defaults.end = new Date(`${defaults.end}/12/31`);
    }
    else if (typeof defaults.end === 'string') {
        defaults.end = new Date(defaults.end.replace(/-/g, '/'));
    }

    const findBy = (array, key, value) => {
        for(let i = 0, len = array.length; i < len; i++){
            const obj = array[i];
            if(obj[key] == value){
                return obj;
            }
        }
    };

    const date = [];
    const interval = cron.parse(defaults.cron, defaults.start, defaults.end);
    let obj;
    do {
        obj = interval.next();

        const year = obj.value.getFullYear();
        const month = obj.value.getMonth() + 1;
        const day = obj.value.getDate();

        let Y = findBy(date, 'value', year);
        if (!Y) {
            Y = {
                label: year + '',
                value: year,
                children: []
            };
            date.push(Y);
        }
        let M = findBy(Y.children, 'value', month);
        if (!M) {
            M = {
                label: month + '',
                value: month,
                children: []
            };
            Y.children.push(M);
        }
        M.children.push({
            label: day + '',
            value: day
        });
    }
    while (!obj.done);

    return picker(date, defaults);
}

export {
    picker,
    datePicker
};



