import Vue from 'vue';
import Router from 'vue-router';

let RouterArr = [
  {name: 'home', path: '/', page: 'index', title: '', hide: true},
  {name: 'base', path: '/base', page: 'component', title: '', children: [
    {name: 'button', path: '/button', page: 'button', title: '()'},
    {name: 'mtButton', path: '/mtButton', page: 'mtButton', title: ''},
    {name: 'cell', path: '/cell', page: 'cell', title: ''},
    {name: 'spinner', path: '/spinner', page: 'spinner', title: ''},
    {name: 'bigPicture', path: '/bigPicture', page: 'bigPicture', title: ''},
    {name: 'loading', path: '/loading', page: 'loading', title: ''},
    {name: 'touchRipple', path: '/touchRipple', page: 'touchRipple', title: ''},
    {name: 'nothing', path: '/nothing', page: 'nothing', title: ''},
    {name: 'icon', path: '/icon', page: 'icon', title: ''},
  ]},
  {name: 'action', path: '/action', page: 'component', title: '', children: [
    {name: 'actionsheet', path: '/actionsheet', page: 'action-sheet', title: ''},
    {name: 'loadmore', path: '/loadmore', page: 'load-more', title: ''},
    {name: 'messageBox', path: '/messageBox', page: 'message-box', title: ''},
    {name: 'swipe', path: '/swipe', page: 'swipe', title: ''},
    {name: 'accordion', path: '/accordion', page: 'accordion', title: ''},
    {name: 'picker', path: '/picker', page: 'picker', title: ''},
    {name: 'tabbar', path: '/tabbar', page: 'tabbar', title: ''},
    {name: 'tabs', path: '/tabs', page: 'tabs', title: ''},
    {name: 'popup', path: '/popup', page: 'popup', title: ''},
    {name: 'toast', path: '/toast', page: 'toast', title: ''},
    {name: 'directives', path: '/directives', page: 'directives', title: ''},
  ]},
  {name: 'form', path: '/form', page: 'component', title: '', children: [
    {name: 'radio', path: '/radio', page: 'radio', title: ''},
    {name: 'checkList', path: '/checkList', page: 'checklist', title: ''},
    {name: 'input', path: '/input', page: 'input', title: ''},
    {name: 'slider', path: '/slider', page: 'slider', title: ''},
    {name: 'uploader', path: '/uploader', page: 'uploader', title: ''},
    {name: 'stepper', path: '/stepper', page: 'stepper', title: ''},
    {name: 'switch', path: '/switch', page: 'switch', title: ''},
    {name: 'search', path: '/search', page: 'search', title: ''},
  ]},
  {name: 'else', path: '/else', page: 'component', title: '', children: [
    {name: 'grid', path: '/grid', page: 'grid', title: ''},
    {name: 'steps', path: '/steps', page: 'steps', title: ''},
    {name: 'numberKeyboard', path: '/numberKeyboard', page: 'numberKeyboard', title: ''},
    {name: 'codebox', path: '/codebox', page: 'codebox', title: ''},
    {name: 'tabContainer', path: '/tabContainer', page: 'tab-container', title: ''},
  ]}
]


function SetRouter(arr){
    let result = []
    for(let i of arr){
        let item = {
            path: i.path,
            name: i.name,
            meta: {
                title: i.title,
                hide: i.hide,
            },
            component: () => import(`../pages/${i.page}.vue`),
        }
        if(i.children && i.children.length){
            item.children = SetRouter(i.children)
        }
        result.push(item)
    }
    return result
}

const router = new Router({
    routes: SetRouter(RouterArr)
});

Vue.use(Router);

export default router;



